import { isRpqlFlatLogicalGroup } from "@rp/common/api-types"
import { RpqlFlatFilter } from "@rp/common/api-types"

export function collectFiltersFromLogicalGroups(
  idsToRemove: Array<string>,
  getFilter: <TFilter extends RpqlFlatFilter = RpqlFlatFilter>(id: string) => TFilter | null,
) {
  const allIds = [...idsToRemove]
  idsToRemove.map(getFilter).forEach(filter => {
    if (filter && isRpqlFlatLogicalGroup(filter)) {
      allIds.concat(filter.value)
    }
  })
  return allIds.map(getFilter)
}
